/* eslint-disable no-console */
import { useEffect, useRef } from 'react';

interface PerformanceMetrics {
  componentName: string;
  renderCount: number;
  lastRenderTime: number;
  averageRenderTime: number;
  maxRenderTime: number;
}

const performanceMetrics = new Map<string, PerformanceMetrics>();

/**
 *  Hook
 * 
 *
 * @param componentName - 
 * @param options - 
 * @param options.enabled - 
 * @param options.logThreshold -  50
 * @param options.slowRenderThreshold - ms 16
 *
 * @example
 * function MyComponent() {
 *   usePerformanceMonitor('MyComponent', { logThreshold: 30 });
 *   // ...
 * }
 */
export function usePerformanceMonitor(
  componentName: string,
  options: {
    enabled?: boolean;
    logThreshold?: number;
    slowRenderThreshold?: number;
  } = {},
) {
  const {
    enabled = process.env.NODE_ENV === 'development',
    logThreshold = 50,
    slowRenderThreshold = 16, // 60fps = 16.67ms per frame
  } = options;

  const renderStartTime = useRef(performance.now());
  const renderCount = useRef(0);

  if (!enabled) return;

  // 
  renderStartTime.current = performance.now();
  renderCount.current += 1;

  useEffect(() => {
    // 
    const renderTime = performance.now() - renderStartTime.current;

    // 
    const metrics = performanceMetrics.get(componentName) || {
      componentName,
      renderCount: 0,
      lastRenderTime: 0,
      averageRenderTime: 0,
      maxRenderTime: 0,
    };

    // 
    metrics.renderCount = renderCount.current;
    metrics.lastRenderTime = renderTime;
    metrics.maxRenderTime = Math.max(metrics.maxRenderTime, renderTime);
    metrics.averageRenderTime =
      (metrics.averageRenderTime * (metrics.renderCount - 1) + renderTime) /
      metrics.renderCount;

    performanceMetrics.set(componentName, metrics);

    // 
    if (renderCount.current % logThreshold === 0) {
      console.warn(
        `[Performance Warning] ${componentName}  ${renderCount.current} `,
        {
          averageRenderTime: `${metrics.averageRenderTime.toFixed(2)}ms`,
          maxRenderTime: `${metrics.maxRenderTime.toFixed(2)}ms`,
          lastRenderTime: `${renderTime.toFixed(2)}ms`,
        },
      );
    }

    // 
    if (renderTime > slowRenderThreshold) {
      console.warn(
        `[Performance Warning] ${componentName} : ${renderTime.toFixed(2)}ms ( ${renderCount.current} )`,
      );
    }
  });
}

/**
 * 
 */
export function getPerformanceMetrics(): Map<string, PerformanceMetrics> {
  return performanceMetrics;
}

/**
 * 
 */
export function printPerformanceReport() {
  console.group(' ');

  const sortedMetrics = Array.from(performanceMetrics.entries()).sort(
    (a, b) => b[1].renderCount - a[1].renderCount,
  );

  sortedMetrics.forEach(([name, metrics]) => {
    console.log(`\n ${name}:`);
    console.log(`  : ${metrics.renderCount}`);
    console.log(`  : ${metrics.averageRenderTime.toFixed(2)}ms`);
    console.log(`  : ${metrics.maxRenderTime.toFixed(2)}ms`);
    console.log(`  : ${metrics.lastRenderTime.toFixed(2)}ms`);

    // 
    if (metrics.renderCount > 100) {
      console.warn(`   `);
    }
    if (metrics.averageRenderTime > 16) {
      console.warn(`   `);
    }
  });

  console.groupEnd();
}

/**
 * 
 */
export function clearPerformanceMetrics() {
  performanceMetrics.clear();
  console.log(' ');
}

// 
if (typeof window !== 'undefined') {
  (window as any).__printPerformanceReport = printPerformanceReport;
  (window as any).__clearPerformanceMetrics = clearPerformanceMetrics;
  (window as any).__getPerformanceMetrics = getPerformanceMetrics;

  console.log(
    ' \n' +
      '  __printPerformanceReport() - \n' +
      '  __clearPerformanceMetrics() - \n' +
      '  __getPerformanceMetrics() - ',
  );
}
