/**
 * Agent 
 *
 * : { type, phase, data }
 */

// ====================  ====================

export enum AgentPhase {
  THINKING = 'thinking', // 
  TOOL_CALLING = 'tool_calling', // 
  GENERATING = 'generating', // 
  COMPLETED = 'completed', // 
  ERROR = 'error', // 
}

// ====================  ====================

export enum EventType {
  PHASE_CHANGE = 'phase_change', // 
  THINKING = 'thinking', // 
  TOOL_START = 'tool_start', // 
  TOOL_END = 'tool_end', // 
  TEXT = 'text', // 
  CITATION = 'citation', // 
  STRUCTURED = 'structured', // 
  DONE = 'done', // 
  ERROR = 'error', // 
}

// ====================  ====================

export interface BaseEvent {
  type: EventType;
  phase: AgentPhase;
  data: any;
}

export interface PhaseChangeEvent extends BaseEvent {
  type: EventType.PHASE_CHANGE;
  data: {
    message: string;
  };
}

export interface ThinkingEvent extends BaseEvent {
  type: EventType.THINKING;
  phase: AgentPhase.THINKING;
  data: {
    content: string;
  };
}

export interface ToolStartEvent extends BaseEvent {
  type: EventType.TOOL_START;
  phase: AgentPhase.TOOL_CALLING;
  data: {
    toolName: string;
    toolDescription: string;
    params?: Record<string, any>;
  };
}

export interface ToolEndEvent extends BaseEvent {
  type: EventType.TOOL_END;
  phase: AgentPhase.TOOL_CALLING;
  data: {
    toolName: string;
    success: boolean;
    summary: string;
    error?: string;
  };
}

export interface TextEvent extends BaseEvent {
  type: EventType.TEXT;
  phase: AgentPhase.GENERATING;
  data: {
    content: string;
  };
}

export interface CitationEvent extends BaseEvent {
  type: EventType.CITATION;
  phase: AgentPhase.GENERATING;
  data: {
    index: number; //  citation 0 
    messageId: string;
    content: string; // 
    senderName: string;
    senderId?: string;
    timestamp: number;
    chatId: string;
  };
}

export interface StructuredEvent extends BaseEvent {
  type: EventType.STRUCTURED;
  phase: AgentPhase.GENERATING;
  data: {
    dataType: string;
    [key: string]: any;
  };
}

export interface DoneEvent extends BaseEvent {
  type: EventType.DONE;
  phase: AgentPhase.COMPLETED;
  data: {
    finishReason: string;
    stats?: {
      totalCitations?: number;
      tokensUsed?: number;
      [key: string]: any;
    };
  };
}

export interface ErrorEvent extends BaseEvent {
  type: EventType.ERROR;
  phase: AgentPhase.ERROR;
  data: {
    code: string;
    message: string;
    recoverable: boolean;
  };
}

// ====================  ====================

export interface AgentStreamOptions {
  showThinking?: boolean; // false
  showToolCalls?: boolean; // true
  detailedCitations?: boolean; // true
}

// ====================  ====================

/**
 * 
 */
export function createEvent(
  type: EventType,
  phase: AgentPhase,
  data: any,
): BaseEvent {
  return { type, phase, data };
}

/**
 *  JSON 
 */
export function serializeEvent(event: BaseEvent): string {
  return JSON.stringify(event) + '\n';
}

/**
 * 
 */
export function createPhaseChangeEvent(
  phase: AgentPhase,
  message: string,
): PhaseChangeEvent {
  return {
    type: EventType.PHASE_CHANGE,
    phase,
    data: { message },
  };
}

/**
 * 
 */
export function createToolStartEvent(
  toolName: string,
  toolDescription: string,
  params?: Record<string, any>,
): ToolStartEvent {
  return {
    type: EventType.TOOL_START,
    phase: AgentPhase.TOOL_CALLING,
    data: { toolName, toolDescription, params },
  };
}

/**
 * 
 */
export function createToolEndEvent(
  toolName: string,
  success: boolean,
  summary: string,
  error?: string,
): ToolEndEvent {
  return {
    type: EventType.TOOL_END,
    phase: AgentPhase.TOOL_CALLING,
    data: { toolName, success, summary, error },
  };
}

/**
 * 
 */
export function createTextEvent(content: string): TextEvent {
  return {
    type: EventType.TEXT,
    phase: AgentPhase.GENERATING,
    data: { content },
  };
}

/**
 * 
 */
export function createCitationEvent(
  index: number,
  messageId: string,
  content: string,
  senderName: string,
  timestamp: number,
  chatId: string,
  senderId?: string,
): CitationEvent {
  return {
    type: EventType.CITATION,
    phase: AgentPhase.GENERATING,
    data: {
      index,
      messageId,
      content,
      senderName,
      senderId,
      timestamp,
      chatId,
    },
  };
}

/**
 * 
 */
export function createDoneEvent(
  finishReason: string,
  stats?: Record<string, any>,
): DoneEvent {
  return {
    type: EventType.DONE,
    phase: AgentPhase.COMPLETED,
    data: { finishReason, stats },
  };
}

/**
 * 
 */
export function createErrorEvent(
  code: string,
  message: string,
  recoverable: boolean = false,
): ErrorEvent {
  return {
    type: EventType.ERROR,
    phase: AgentPhase.ERROR,
    data: { code, message, recoverable },
  };
}
